.pagination {
    display: inline-block;
    vertical-align: top;
    width:100%;
    height:60px;
    padding-bottom: 10px;
}
.pagination .page-item {
    display: inline-block;
    text-align: center;
    font-size: small;
    padding: 0;
    width: 35px;
    text-decoration: none;
    color: black;
}
.pagination .prev-item {
    display: inline-block;
    text-align: center;
    font-size: small;
    padding: 0;
    width: 50px;
    text-decoration: none;
    color: black;
}
.pagination .next-item {
    display: inline-block;
    text-align: center;
    font-size: small;
    padding: 0;
    width: 50px;
    text-decoration: none;
    color: black;
}
.pagination a {
  text-decoration: none;
  color: #429BF9;
}
.pagination a:hover {
  text-decoration: underline;
}
.pagination .active-item a {
  color: #000000;
}
.text_highlight {
  background: #FFFFFF;
  color: #000000;
  font-weight: bold;
}
@media screen and (max-width: 1024px) {
.s_results{
    grid-column: span 12;
    font-family: arial, sans-serif;
    margin: 15px 165px 51px;
}
.page_number{
    margin-top: 15px;
    margin-left: 50px;
}
}
@media screen and (max-width: 768px) {
.search input[type="text"]{
    -webkit-box-sizing:border-box;
