}
}

html, body {
    width: 100%;
    	margin: 0;
        padding: 0;
        border: none;
        height: 100%;
        background: transparent;
        font-size: 13px;
        display: block;
        font-family: arial, sans-serif;
}
body{
	margin: 0;
}
.grid {
  min-height: 100%;
	display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: auto 1fr auto;
}
.header{
  grid-column: span 12;
	display:relative;
	padding-top: 18px;
	padding-left: 13px;
	padding-right: 35px;
	background-color: #FAFAFA;
	height: 70px;
	border-bottom: 0.5px solid #EBEBEB;
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
}
